import React from 'react';
import { Link } from 'react-router';

import SlotSelector from '../../components/SlotSelector';
import DaySelector from '../../components/DaySelector';

import { Wrapper, Block, Logo, Header2, Title2, Button3, Dates, Center, Title3 }
  from '../../components/styledComponents';
import ProgressBar from '../../components/order/ProgressBar';
import LogoImg from '../../assets/logo1.png';


const Step4 = () => (
  <Wrapper>
    <Block>
      <Header2>
        <Logo src={LogoImg} />
        <ProgressBar step={3} />
        <Title2>
          Mon Rendez-vous
        </Title2>
      </Header2>
      <Dates>
        <Title3>Jour</Title3>
        <DaySelector />
        <Title3>Heure</Title3>
        <SlotSelector />
      </Dates>
      <Center>
        <Link to={'/order/step5'}><Button3>{'> suivant <'}</Button3></Link>
      </Center>
    </Block>
  </Wrapper>
);

export default Step4;



